@import url(https://fonts.googleapis.com/css?family=Azeret+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-container {
    padding: 5px;
    background-color: rgba(150, 70, 47, 1);
    text-align: center;
    color: white;
}

.navbar-container h2 {
    font-weight: bold;
}
* {
  font-family: 'Azeret Mono';
  color: white;
}

body {
  background-color: #333;
}


.selector-container {
    display: flex;
    justify-content: center;
    padding: 20px;
    background-color: rgba(150, 70, 47, 1);
}

.selector-item {
    color: #AAA;
    padding: 0px 20px;
    font-size: 1.2rem;
    cursor:pointer
}
.selector-item.selected {
    color: #FFF;
    text-decoration: underline;
}
.payment-container {
    padding-top: 20px;
    width: 50%;
    margin: auto;
}

.payment-input-container {
    display: flex;
}

.payment-file-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.payment-container h2 {
    text-align: center;
}

.payment-input {
    background-color: #555;
    width: 90%;
    font-size: 1.3rem;
    color: white;
    flex: 1 1;
}

.payment-container textarea {
    height: 200px;
    font-size: 0.8rem;
}

.payment-header {
    color: #AAA;
    padding-bottom: 5px;
}

.payment-container .status-indicator {
    text-align: center;
}

.success {
    color: greenyellow;
}

.failure {
    color: red;
}

button {
    color: black;
    background-color: #bbb;
    padding: 5px 10px;
    border-radius: 1px;
    font-size: 1rem;
    border: none;
}
.payment-container .submit-button {
    margin: 0px auto;
}

.payment-button-container {
    display: flex;
}


@media only screen and (max-width: 900px) {
        .payment-container {
            width: 92%;
        }
}
.explorer-container {
    padding: 20px 0px;
    width: 90%;
    margin: auto;
}
.main-blocks-container {
    display: flex;
    justify-content: space-between;
}

.main-block-content {
    width: 100%;
    background-color: #444;
    margin: auto;
    box-sizing: border-box;
    padding: 20px;
    word-wrap: break-word;
}

.verify-button-label {
    display: flex;
    justify-content: space-between;
    width: 99%;
    margin: auto;
    text-align: right;
    padding-bottom: 10px;
    
}

.verify-button-label h4 {
    color: coral;
    cursor: pointer;
}

.main-block-container-verify {
    text-align: center;
}

.main-block-container-verify h3, p {
    color: #999;
}

.main-block-content-label {
    padding-bottom: 10px;
}

.main-block-transactions-table {
    margin-top: 20px;
    text-align: left;
    border-collapse: collapse;
    table-layout:fixed;
    width:100%;
}

.main-block-transactions table, th, td {
    border: 5px solid #444;
    padding: 10px;
    border-collapse: collapse;
    background-color: #555;
}

.main-block-title {
    text-align: center;
    font-style: bold;
    font-size: 1.5rem;
}

.main-block-header {
    text-align: center;
    font-size: 0.8rem;
    color: coral;
}

@media only screen and (max-width: 900px) {
    .explorer-container {
        width: 100%;
    }
    .verify-button-label {
        width: 90%;
    }
        .block-container {
            width: 99%;
        }
        .main-block-content {
            width: 99%;
        }
        .main-block-transactions-table {
            overflow-x: auto;
            display: block;
        }

}
.block-container span {
    color: violet
}

.block-container .content {
    padding-bottom: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.block-container h3 {
    font-weight: bold;
}

.block-container h4 {
    font-weight: bold;
}

.block-container .title {
    font-size: 1.4rem;
}

.block-container {
    background-color: #555;
    overflow-wrap: break-word;
    padding: 20px;
    width: 375px;
    margin: auto;
    cursor: pointer;
}

.main-block-container .selected {
    background-color: rgb(60, 99, 61)
}

.block-container .title-header {
    font-size: 0.8rem;
    color: coral;
}


@media only screen and (max-width: 900px) {
        .block-container {
            width: 80%;
        }
}
.pos-main-container {
    display: flex;
    align-items: flex-start;
    width: 90%;
    /* background-color: red; */
    margin: 20px auto;
    grid-gap: 15px;
    gap: 15px;
}

.add-node-container {
    padding: 20px;
    background-color: #333;
    width: 20%;
    /* min-width: 200px; */
}

.nodes-container {
    padding: 20px;
    background-color: #333;
    width: 75%;
}

.pos-node-container {
    padding: 10px;
    margin-right: 10px;
    background-color: #555;
}

.pos-main-container .selected {
    background-color: rgb(60, 99, 61)

}


.node-input-container {
    display: flex;
}

.node-file-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
/* .node-stake-table {
    width: 100%;
} */
.node-stake-table {
    width: 100%;
    margin-top: 20px;
    /* border: 1px solid black; */
    text-align: left;
    border-collapse: collapse;
    table-layout:fixed;
    width:100%;
}

.node-stake-table table, th, td {
    border: 5px solid #333;
    padding: 10px;
    /* border-collapse: collapse; */
    background-color: #555;
}

.add-node-container h2 {
    text-align: center;
}

.node-input {
    background-color: #555;
    width: 90%;
    font-size: 1.3rem;
    color: white;
    flex: 1 1;
}

.node-header {
    color: #AAA;
    padding-bottom: 5px;
}

.add-node-container .status-indicator {
    text-align: center;
}

.success {
    color: greenyellow;
}

.failure {
    color:coral;
}

button {
    color: black;
    background-color: #bbb;
    padding: 5px 10px;
    border-radius: 1px;
    font-size: 1rem;
    border: none;
}
.add-node-container .submit-button {
    margin: 0px auto;
}

.node-button-container {
    display: flex;
}


@media only screen and (max-width: 1200px) {
    .pos-main-container {
        /* display: flex; */
        /* flex-direction: column; */
        display: block;
        width: 100%;
        margin: auto;
        margin-top: 20px;
    }

    .add-node-container {
        background-color: #333;
        width: 100%;
        box-sizing: border-box;
        margin-top: 10px;
    }

    .nodes-container {
        box-sizing: border-box;
        width: 100%;
        background-color: #333;
    }
    .node-stake-table {
            width: 100%;
            overflow-x: auto;
            display: block;
        }
}
